@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One&family=Oswald:wght@700&family=Righteous&display=swap');

.animate-heading {
    font-family: 'Righteous', cursive;

   // font-family: 'Black Ops One', cursive;
	//font-family: "Oswald", sans-serif;
	background-image: url("https://assets.codepen.io/2970881/kassandras__vibrant_flower_pattern_colorful_6f2e398e-b0b7-433f-830e-2e297f9c9f93.png");
	color: #fff;
	color: transparent;
	background-size: contain;
	-webkit-background-clip: text;
	filter: drop-shadow(0px -3px 1px rgba(0, 0, 0, 1));
	animation: stripes 5s infinite alternate;
}
@keyframes stripes {
	100% {
		background-position: 100px 0, 100px 0, 100px 0;
   }
}